import service from '../../service.js'
export default{
	data(){
		return{
			show:true,
			imgSrc1:'',
			imgSrc2:'',
			photo1:true,
			photo2:true,
			realname:'',
			idnum:'',
			idimg1:'',
			idimg2:'',
			
		}
	},
	methods:{
		upload(type){
			let that = this
			console.log(type)
			if(type == 1){
				uni.chooseImage({
					count:1,
					success(res) {
						uni.uploadFile({
							url:service.api_root.user.updateIdImage+'?token='+uni.getStorageSync('token'),
							filePath:res.tempFilePaths[0],
							name:'idimg1',
							formData:{
								filename:'idimg1'
							},
							success: function (reg) {
								// console.log(reg)
								// console.log(JSON.parse(reg.data))
								let reg_data = JSON.parse(reg.data)
								uni.showToast({
									icon:'none',
									title:reg_data.msg
								})
								if(reg_data.code == 200){
									that.photo1 = false
									that.imgSrc1 = service.api+reg_data.data.path
									that.idimg1 = reg_data.data.path
								}
							}
						})
					}
				})
			}else{
				uni.chooseImage({
					count:1,
					success(res) {
						that.imgSrc2 = res.tempFilePaths[0]
						that.photo2 = false
						uni.uploadFile({
							url:service.api_root.user.updateIdImage+'?token='+uni.getStorageSync('token'),
							filePath:res.tempFilePaths[0],
							name:'idimg2',
							formData:{
								filename:'idimg2'
							},
							success: function (reg) {
								// console.log(reg)
								// console.log(JSON.parse(reg.data))
								let reg_data = JSON.parse(reg.data)
								uni.showToast({
									icon:'none',
									title:reg_data.msg
								})
								if(reg_data.code == 200){
									that.photo2 = false
									that.imgSrc2 = service.api+reg_data.data.path
									that.idimg2 = reg_data.data.path
								}
							}
						})
					}
				})
			}
			
		},
		confirm(){
			let that = this
			uni.request({
				url:service.api_root.user.upIdentityCard+'?token='+uni.getStorageSync('token'),
				method:'post',
				data:{
					// uid:uni.getStorageSync('info').uid,
					realname:that.realname,
					idnum: that.idnum,
					idimg1:that.idimg1,
					idimg2:that.idimg2
				},
				success(res) {
					console.log(res)
				}
			})
		}
	},
	onShow() {
		let that = this
		service.entire(this,service.api_root.user.realname,{token:uni.getStorageSync('token')},function(self,res){
			console.log(res)
			that.show = res.data
		})
	}
}
